import { RouterModule, Routes } from '@angular/router'
import { LoginComponent } from '../app/login/login.component'
import { PagenotfoundComponent } from '../app/pagenotfound/pagenotfound.component';
import { ParentLoginComponent } from '../app/parent.login/parent.login.component';
import { AdminLoginComponent } from '../app/admin.login/admin.login.component';
import { LogoutComponent } from '../app/logout/logout.component';
import { BusDetailsComponent } from '../app/bus.details/bus.details.component';
import { StudentRegistrationComponent } from '../app/student.registration/student.registration.component';

const route: Routes = [
  {
    path: '',
    redirectTo: '/home',
    pathMatch: 'full'
  },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'parentLogin',
    component: ParentLoginComponent
  },
  {
    path: 'adminLogin',
    component: AdminLoginComponent
  },
  {
    path: 'logout',
    component: LogoutComponent
  },
  {
    path: 'busdetails',
    component: BusDetailsComponent
  },
  {
    path: 'studentregstr',
    component: StudentRegistrationComponent
  },
  {
    path: '**',
    component: PagenotfoundComponent
  }
];

export const Approutes = RouterModule.forRoot(route);


